html {
  font: 100px Arial, sans-serif;
}

body {
  margin: 0;
}

.container {
  display: flex;
  flex-direction: column;
}

.block {
  display: flex;
  height: 300px;
  color: white;
  text-align: center;
  justify-content: center;
  align-items: center;
  background-color: rgb(255, 0, 0);
}

.block-2 {
  background-color: rgb(204, 0, 0);
}

.block-3 {
  background-color: rgb(153, 0, 0);
}

.block-4 {
  background-color: rgb(102, 0, 0);
}

.block-5 {
  background-color: rgb(51, 0, 0);
}

.block-6 {
  background-color: rgb(0, 0, 0);
}

@media screen and (min-width: 600px) {
  .container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .block {
    flex-basis: 300px;
    flex-grow: 1;
  }

  .block-3 {
    order: 2;
  }
  
  .block-4 {
    order: 1;
  }

  .block-5 {
    order: 3;
  }
  
  .block-6 {
    order: 4;
  }
}

@media screen and (min-width: 900px) {
  .block {
    flex-basis: 33%;
  }

  .block-3 {
    order: 1;
  }
  
  .block-4 {
    order: 4;
  }

  .block-5 {
    order: 3;
  }
  
  .block-6 {
    order: 2;
  }
}
